/* overwrite tom-select defaults */
.ts-control {
  @apply border-gray text-md min-h-[40px];
}
.ts-control input {
  @apply font-normal text-black text-md;
}
.ts-dropdown {
  @apply text-md text-gray-2 font-normal;
}
.ts-dropdown .active {
  @apply text-white bg-secondary;
}

#proposal-voting-rules [id=dropdown-menu-proposal-voting-rules][aria-hidden=true] {
  @apply hidden;
}
#proposal-voting-rules [data-target=dropdown-menu-proposal-voting-rules] {
  @apply w-full flex items-center justify-between gap-2 p-2 first-of-type:[&>svg]:block last-of-type:[&>svg]:hidden;
}
#proposal-voting-rules [data-target=dropdown-menu-proposal-voting-rules] > span {
  @apply hidden font-semibold text-secondary;
}
#proposal-voting-rules [data-target=dropdown-menu-proposal-voting-rules] > span:only-of-type, #proposal-voting-rules [data-target=dropdown-menu-proposal-voting-rules] > span.is-active {
  @apply flex items-center gap-2 [&_svg]:fill-current;
}
#proposal-voting-rules [data-target=dropdown-menu-proposal-voting-rules] > svg {
  @apply w-6 h-6 flex-none text-secondary fill-current;
}
#proposal-voting-rules [data-target=dropdown-menu-proposal-voting-rules][aria-expanded=false] > svg:last-of-type, #proposal-voting-rules [data-target=dropdown-menu-proposal-voting-rules][aria-expanded=true] > svg:first-of-type {
  @apply hidden;
}
#proposal-voting-rules [data-target=dropdown-menu-proposal-voting-rules][aria-expanded=true] > svg:last-of-type, #proposal-voting-rules [data-target=dropdown-menu-proposal-voting-rules][aria-expanded=false] > svg:first-of-type {
  @apply block;
}

.layout-item-complementary {
  @apply container grid grid-cols-1 lg:grid-cols-12;
}
.layout-item-complementary .item_voting_rules {
  @apply lg:col-start-2 lg:col-span-10;
}

.proposal__aside-vote button[disabled] {
  @apply bg-[var(--secondary)] opacity-50 text-white border-current cursor-not-allowed;
}

.card__proposals-item + .card__proposals-item {
  @apply mt-6;
}